// Constants
import { platforms } from '../../constants'

/**
 * The 'connection' namespace is used to connect and maintain connections between
 * the SDK and one or more backend servers.
 *
 * @public
 * @namespace connection
 */

import { changeConnectivityChecking } from './actions'
import { getConnectionState } from './selectors'

import { logManager, API_LOG_TAG } from '../../logs'

const log = logManager.getLogger('CONNECTIVITY')

export default function api ({ dispatch, getState }) {
  const connectivityApi = {
    /**
     * Get the state of the websocket.
     * @public
     * @memberof connection
     * @method getSocketState
     * @param {string} [platform='link'] Backend platform for which to request the websocket's state.
     */
    getSocketState (platform = platforms.LINK) {
      log.debug(API_LOG_TAG + 'connection.getSocketState: ', platform)
      return getConnectionState(getState(), platform)
    },

    /**
     * Enables or disables connectivity checking.
     * @public
     * @memberof connection
     * @method enableConnectivityChecking
     * @param {boolean} enable Enable connectivity checking.
     */
    enableConnectivityChecking (enable) {
      log.debug(API_LOG_TAG + 'connection.enableConnectivityChecking: ', enable)
      dispatch(changeConnectivityChecking(enable))
    }
  }
  return { connection: connectivityApi }
}
