// Connectivity plugin.
import { openWebsocket, closeWebsocket, wsEmitter } from './websocket'
import { getConnectivityConfig, getPingInterval, getCheckConnectivity } from '../interface/selectors'
import * as actionTypes from '../interface/actionTypes'
import * as actions from '../interface/actions'

// Other plugins.
import { getConnectionInfo } from '../../auth/interface/selectors'
import { getSubscriptionInfo } from '../../subscription/interface/selectors'
import { logManager } from '../../logs'

// Libraries.
import { takeEvery, delay, call, put, fork, cancel, actionChannel, race, select, take } from 'redux-saga/effects'

// Constants
import { platforms, connCheckMethods, connCheckResponsibility } from '../../constants'

// Get the logger
const log = logManager.getLogger('CONNECTIVITY')

export function * wsConnectFlow () {
  log.info('Creating channel for handling websocket actions ...')
  const chan = yield actionChannel(actionTypes.WS_ATTEMPT_CONNECT)
  log.info('Channel successfuly created.')
  yield takeEvery(chan, websocketLifecycle)
}

/**
 * Saga that handles a websocket over its lifecycle.
 * @method websocketLifecycle
 * @param  {Object} wsConnectAction
 */
export function * websocketLifecycle (wsConnectAction) {
  const wsInfo = wsConnectAction.payload
  const { platform, isReconnect } = wsConnectAction.meta

  log.info(`Connecting to websocket on platform: ${platform} ...`)
  // Try to open the websocket.
  const websocket = yield call(connectWebsocket, wsInfo, platform)

  // Append information to the websocket, so that its accessible elsewhere.
  // TODO: Remove this and replace with proper redux state storage.
  websocket.kandy = yield select(getConnectivityConfig)

  websocket.kandy.wsInfo = wsInfo
  websocket.kandy.platform = platform

  // If the websocket didn't open, dispatch the error and stop here.
  if (websocket.error) {
    if (isReconnect) {
      yield put(actions.wsReconnectFailed(undefined, platform))
      return
    } else {
      yield put(actions.wsConnectFinished(new Error(websocket.message), platform))
      return
    }
  }

  // set last contact in both cases to be now
  websocket.kandy.lastContact = Date.now()

  const emitTask = yield fork(wsEmitter, websocket, platform)

  // determine which pingFlow is appropriate
  let pingFlow
  if (websocket.kandy.method.responsibleParty === connCheckResponsibility.SERVER) {
    log.debug(`Starting a serverPing flow on ${platform} ...`)
    pingFlow = yield fork(serverPingFlow, websocket, platform)
  } else {
    log.debug(`Starting a clientPing flow on ${platform} ...`)
    pingFlow = yield fork(clientPingFlow, websocket, platform)
  }

  // Dispatch the finish action, to notify that the websocket is connected.
  yield put(actions.wsConnectFinished(websocket, platform))

  // Redux-saga take() pattern.
  // Take end of lifecycle WS actions for this platform.
  function closeWebsocketPattern (action) {
    return (
      (action.type === actionTypes.WS_DISCONNECT || action.type === actionTypes.LOST_CONNECTION) &&
      action.meta.platform === platform
    )
  }

  // Wait for a disconnect or lost connection action.
  const action = yield take(closeWebsocketPattern)
  log.debug(`Cancelling outstanding tasks upon receiving action: ${action.type}`)

  // Whether we're disconnecting or have lost connection,
  //      we want to cancel these tasks either way.
  yield cancel([emitTask, pingFlow])

  if (action.type === actionTypes.WS_DISCONNECT) {
    // If we're disconnecting, close the websocket to end it's lifecycle.
    log.debug('Closing websocket connection ...')
    yield call(closeWebsocket, websocket)
    yield put(actions.wsDisconnectFinished(undefined, platform))
    log.info('Successfully closed websocket connection.')
  } else {
    // If this is a Link websocket, we need to ensure the URL is using the
    //     "latest" access token from state.
    if (wsConnectAction.meta.platform === platforms.UC) {
      log.info('Updating access token ...')
      const { notificationChannel } = yield select(getSubscriptionInfo)
      const { accessToken, oauthToken } = yield select(getConnectionInfo)
      wsInfo.url = notificationChannel
      if (oauthToken && !accessToken) {
        wsInfo.params = {
          access_token: oauthToken
        }
      } else {
        wsInfo.params = {
          token: accessToken
        }
      }
    } else if (wsConnectAction.meta.platform === platforms.LINK) {
      const { bearerAccessToken } = yield select(getConnectionInfo)
      wsInfo.params = {
        token: bearerAccessToken
      }
    }

    // If we've lost connection, re-dispatch the initial action, so that we can
    //      start the lifecycle over.
    log.debug('Attempting to reconnect to websocket ...')
    yield put(actions.wsAttemptConnect(wsInfo, wsConnectAction.meta.platform, true))
  }
}

/**
 * if we receive a server ping we want to respond with a pong,
 * if there is none we want to check to see if its been too long since the server last pinged us and if it has been then reconnect,
 * otherwise we just want to increment the time since the last ping
 * @param {Object} ws a websocket connected to the backend
 * @return {Object} yields a Flux standard action
 */
export function * serverPingFlow (ws) {
  const { lastContact, platform, maxMissedPings, autoReconnect } = ws.kandy
  let timeOfLastPing = Date.now()

  while (true) {
    let pingInterval = yield select(getPingInterval)
    pingInterval = typeof pingInterval !== 'undefined' ? pingInterval : 120000
    const maxIdleDuration = pingInterval * maxMissedPings
    log.debug(`serverPing: Using ping interval: ${pingInterval} with maximum idle duration: ${maxIdleDuration}`)

    // wait for incoming server pings or disconnect actions on an interval
    const { serverPing, disconnect } = yield race({
      expiry: delay(pingInterval),
      serverPing: take(actionTypes.RECEIVE_SERVER_PING),
      disconnect: take(actionTypes.WS_DISCONNECT_FINISHED)
    })

    // is disconnect action received then exit
    if (disconnect) {
      log.debug('Got disconnect action. Exiting from serverPing flow ...')
      break
    }

    if (serverPing) {
      // server sent us a ping so reset the timeSinceLastPing
      timeOfLastPing = Date.now()

      // pingInterval is stored in milliseconds but comes in as seconds so convert
      const pingIntervalMillis = serverPing.payload.connCheck.interval * 1000
      // Prevent firing actions if pingInterval hasn't changed
      if (pingIntervalMillis !== pingInterval) {
        yield put(actions.changePingInterval(pingIntervalMillis, platform))
      }

      const message = { connAck: {} }
      log.debug(
        `Received a server ping which uses interval: ${pingIntervalMillis} ms. ${platform} is replying with message: ${message}`
      )
      const error = _sendWSMessage(ws, JSON.stringify(message))

      // if the pong websocket message has an error then try to reconnect
      if (error) {
        log.error(`Got error when attempting to reply: ${error.message}`)
        if (autoReconnect) {
          log.info('Trying to auto reconnect ...')
          yield put(actions.lostConnection(undefined, platform))
        }
        break
      }
    } else {
      if (Date.now() - timeOfLastPing >= maxIdleDuration) {
        log.warn('Closing websocket due to inactivity. (have not received pong from server)', platform)

        // try to reconnect or exit
        if (autoReconnect) {
          log.debug(`${platform} is attempting to auto reconnect ...`)
          yield put(actions.lostConnection(undefined, platform))
        }
        break
      } else {
        log.debug(`${platform} websocket last contact: ${lastContact}. Reconnect after ${maxIdleDuration}.`)
      }
    }
  }
}

/**
 * This flow is responsible for handling keepAlive and pingPong methods for client,
 * if keepAlive is used the client simply sends the keepAlive message to the websocket after waiting the pingInterval
 * if pingPong is used the client is responsible for pinging the server and listening for server "pong" responses via the websocket
 * the client will then determine if too much time has passed and attempt to reconnect if its not receiving server pongs or if an error occurs
 * @param {Object} ws a websocket connected to the backend
 * @return {Object} yields a Flux standard action
 */
export function * clientPingFlow (ws) {
  const { lastContact, platform, maxMissedPings, autoReconnect } = ws.kandy
  const { checkConnectivity, method } = yield select(getConnectivityConfig)

  let timeOfLastPong = Date.now()
  let message = ''
  let shouldCheck

  while (true) {
    // If the flag was explicitly set in state, prefer that over the config.
    shouldCheck = yield select(getCheckConnectivity)
    shouldCheck = typeof shouldCheck !== 'undefined' ? shouldCheck : checkConnectivity

    const { pingInterval } = yield select(getConnectivityConfig)
    const maxIdleDuration = pingInterval * maxMissedPings
    const intervalInSeconds = pingInterval / 1000

    if (shouldCheck) {
      if (method.type === connCheckMethods.PING_PONG) {
        message = { connCheck: { interval: intervalInSeconds } }
      } else if (method.type === connCheckMethods.KEEP_ALIVE) {
        message = { message_type: 'ping' }
      } else {
        log.error(`Invalid connectivity method: ${method}`)
        break
      }

      log.debug(`${platform} is sending a ${method.type} every ${intervalInSeconds} sec.`)

      const error = _sendWSMessage(ws, JSON.stringify(message))
      if (error) {
        log.error('Exception in clientPing flow: ' + error.message)
        if (autoReconnect) {
          log.debug(`${platform} is attempting to auto reconnect ...`)
          yield put(actions.lostConnection(undefined, platform))
        }
        break
      }
    } else {
      log.debug('Set to not check websocket connectivity. Waiting for connectivity status change ...')

      // If we shouldn't ping, wait until we receive a trigger to (maybe) ping.
      const shouldCheckConnectivity = yield take(
        action => action.type === 'CHANGE_CONNECTIVITY_CHECKING' && action.payload
      )
      log.debug(`Connectivity check setting changed. Check connectivity?: ${shouldCheckConnectivity}`)
      continue
    }

    const delayStartTime = Date.now()
    // Wait for either the ping interval, a disconnect or a pong from the server
    const { disconnect, serverPong } = yield race({
      expiry: delay(pingInterval),
      serverPong: take(actionTypes.RECEIVE_SERVER_PONG),
      disconnect: take(actionTypes.WS_DISCONNECT_FINISHED)
    })

    // If we receive a serverPong, we will not have waited the full pingInterval duration.
    //    Determine how long we were waiting for, so that we know how long to wait (again)
    //    to fulfill the full pingInterval duration (before restarting the ping loop).
    const timeElapsed = Date.now() - delayStartTime

    // If we received a disconnect action, stop the pings and exit.
    if (disconnect) {
      log.debug('Got disconnect action. Exiting from clientPing flow ...')
      break
    }

    if (method.type === connCheckMethods.PING_PONG) {
      if (serverPong) {
        // we received a pong, wait pingInterval to send another ping
        timeOfLastPong = Date.now()

        const timeUntilNextPing = pingInterval - timeElapsed
        const { disconnect } = yield race({
          expiry: delay(timeUntilNextPing),
          disconnect: take(actionTypes.WS_DISCONNECT_FINISHED)
        })
        // If we received a disconnect action, stop the pings and exit.
        if (disconnect) {
          log.debug('Got disconnect action. Exiting from clientPing flow ...')
          break
        }
      } else {
        if (Date.now() - timeOfLastPong >= maxIdleDuration) {
          log.warn('Closing websocket due to inactivity. (have not received pong from server)', platform)

          // its been too long since the last pong, attempt to reconnect or exit
          if (autoReconnect) {
            yield put(actions.lostConnection(undefined, platform))
          }

          break
        } else {
          log.debug(`${platform} websocket last contact: ${lastContact}. Reconnect after ${maxIdleDuration}.`)
        }
      }
    }
  }
}

/**
 * Generic Websocket message that takes a websocket and a message and sends it
 * @param  {Object} ws  websocket
 * @return {Object}     Error object
 */
function _sendWSMessage (ws, message) {
  try {
    if (ws && ws.readyState === 1) {
      log.debug('Sending message on websocket.', message)
      ws.send(message)
    } else {
      throw new Error('websocket was not in readyState')
    }
  } catch (e) {
    return e
  }
}

/**
 * Helper function for connecting to a websocket.
 * Attempts to connect a specified number of times before returning an error.
 * Includes a delay in between attempts, determined by configs.
 * @method connectWebsocket
 * @param  {Object} wsInfo
 * @return {Websocket|Object}  Either a connected websocket or an error object.
 */
export function * connectWebsocket (wsInfo, platform) {
  const configs = yield select(getConnectivityConfig)
  let connectionAttempt = 0
  let delayTime = 0
  let websocket

  // Redux-saga take() pattern.
  // Take disconnect websocket action for this platform.
  function disconnectWebsocketPattern (action) {
    return action.type === actionTypes.WS_DISCONNECT && action.meta.platform === platform
  }

  // If no limit is set, we will continually attempt to reconnect.
  if (!configs.reconnectLimit) {
    log.debug('No connectivity reconnect limit set.')
  }

  while (connectionAttempt < configs.reconnectLimit || !configs.reconnectLimit) {
    try {
      // Try to open the websocket. Blocking call.
      websocket = yield call(openWebsocket, wsInfo)
      log.info(`Successfully connected to websocket on: ${platform}`)
      break
    } catch (err) {
      connectionAttempt++
      websocket = err
      log.debug(
        `Failed to connect to websocket on ${platform}. (Attempt #${connectionAttempt}). Message: ${websocket.message}.`
      )

      // If we want to try to reconnect, delay a certain about of time before trying.
      if (connectionAttempt < configs.reconnectLimit || !configs.reconnectLimit) {
        // Increase the delay time if we're not at the limit.
        if (delayTime !== configs.reconnectTimeLimit) {
          delayTime = configs.reconnectDelay * Math.pow(configs.reconnectTimeMultiplier, connectionAttempt - 1)
          delayTime = delayTime < configs.reconnectTimeLimit ? delayTime : configs.reconnectTimeLimit
        }
        log.debug(`Websocket reconnect attempt after ${delayTime} ms on ${platform}`)

        // Wait for either the delay period or a trigger to stop connection attempts.
        const { disconnect } = yield race({
          delay: delay(delayTime),
          disconnect: take(disconnectWebsocketPattern)
        })

        if (disconnect) {
          break
        }
      } else {
        log.debug(`Stopping websocket connection attempts on ${platform}.`)
      }
    }
  }

  // Return either the websocket or the latest error.
  return websocket
}
